
// ===== pagination ===== 

.pagin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.pagin__arrow {
  display: none;
  color: #6D71F9;
  transition: .2s ease 0s;
}

.pagin__list {
  display: flex;
  justify-content: center;
}

.pagin__list li {
  margin: 0px 5px;
}

.pagin__list a,
.pagin__list span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 18px;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: .2s ease 0s;
}

.pagin__list a.active {
  border-color: #6D71F9;
  pointer-events: none;
  cursor: default;
}

@media (pointer: fine),
(min-width:0\0) {
  .pagin__arrow:hover {
    color: $color-hover-1;
  }

  .pagin__list a:hover:not(.active) {
    border-color: $color-hover-1;
    // color: #e9f0f6;
    // background-color: #90a8be;
  }
}

.pagin__list a:active {
  // background-color: #271d67;
}

@media (min-width: 480px) {
  .pagin__arrow {
    display: block;
  }

  .pagin__list a,
  .pagin__list span {
    // width: 60px;
    // height: 60px;
  }
}