// ===== socials ===== 

.socials {
  & ul {
    display: flex;
    justify-content: center;
  }

  & li+li {
    // margin-left: 20px;
  }

  & a {
    display: flex;
    justify-content: center;
    align-items: center;
    // width: 50px;
    // height: 50px;
    font-size: 24px;
    // color: $color-1;
    // border-radius: 50%;
    // border: 1px solid #fff;
    transition: .2s ease 0s;
  }

  @media (pointer: fine) {
    & a:hover {
      // color: $color-hover-1;
      background: $color-hover-1;
    }
  }

  & a:active {
    // color: $color-active-1;
    background-color: $color-active-1;
  }

  & svg {
    display: block;
    width: 18px;
    height: 18px;
  }
} 