// ===== btn ===== 

.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: rem(40);
  padding: 5px 20px;
  font-size: rem(14);
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  // border: 2px solid #6D71F9;
  border-radius: 6px;
  background-color: $color-1;
  box-shadow: 6px 6px 12px rgba(213, 35, 49, 0.25);
  transition: .2s;

  &::before {
    margin-right: 10px;
  }

  @media (min-width: $md) {
    min-height: rem(50);
  }


  @media (pointer: fine) {
    &:hover {
      // color: $color-hover-1;
      background-color: $color-hover-1;
    } 
  }

  &:active {
    // color: $color-active-1;
    background-color: $color-active-1;
  }

  & .icon {
    flex-shrink: 0;
  }
}