// == breadcrumb == 

.breadcrumb {
  margin-bottom: 20px;
  overflow-x: auto;
}

.breadcrumb ul {
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  color: #fff;
  font-size: 14px;
}

.breadcrumb-2 ul {
  color: #B8B9D0;
}

.breadcrumb li+li:before {
  content: "-";
  margin: 0 10px;
}

@media (pointer: fine),
(min-width:0\0) {
  .breadcrumb a:hover {
    color: $color-hover-1;
  }
}

.breadcrumb a:active {
  color: #2DB6B1;
}

.breadcrumb-2 {
  margin-bottom: 10px;
}

.breadcrumb-2 ul {
  justify-content: left;
  @media (min-width: 1200px) {
     justify-content: center;
  }
}