// ==== select ==== 

@if 1 {

  .select-wrap {
    position: relative;
  }

  .select-wrap:after,
  .select-custom {
    z-index: 3;
  }

  // arrow
  .select-wrap:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 20px;
    display: block;
    // border: 5px solid transparent;
    // border-top-color: #C0C5D1;
    padding: 3px;
    border: solid #fff;
    border-width: 0 0 2px 2px;
    transform: rotate(-45deg) translateY(-6px);
    pointer-events: none;
  }

  .select-wrap.focus:after,
  .select-wrap.isActive:after,
  .select-wrap.oupen:after {
    // border-top-color: $color-hover-1;
  }

  .select-wrap.isActive:after,
  .select-wrap.oupen:after {
    // transform: rotate(180deg) translateY(5px);
    transform: rotate(135deg) translateY(6px);
  }


  .select,
  .select-custom {
    width: 100%;
    height: rem(40);
    font-size: rem(14);
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-family: $font-2;

    @media (min-width: 768px) {
      height: rem(50);
      // font-size: rem(16);
    }

    @media (min-width: 1200px) {
      // font-size: rem(18);
    }
  }

  .select {
    position: relative;
    display: block;
    // box-shadow: -12px -12px 24px #7D82FF, 12px 12px 24px #5D60D4;
    -webkit-appearance: none;
    appearance: none;
    // opacity: 0;
  }

  .select-custom {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
  }

  .select,
  .select-custom__btn {
    padding: 0px 20px 0px 20px;
    color: #fff;
    font-weight: 700;
    background-color: rgba(#fff, .15);
    border: 1px solid transparent;
    border-radius: 6px;
    transition: .1s ease 0s;
  }

  .select-custom__btn {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }

  .select-custom__btn::before,
  .select-custom__option::before {
    margin-right: 10px;
  }

  .select:focus,
  .select-custom.isActive .select-custom__btn {
    color: #fff;
    background-color: rgba(#fff, .3);
    // border-color: $color-hover-1;
    border-color: #fff;
    transition: .1s ease 0s;
  }

  .select:invalid,
  .select-custom__btn.invalid {
    // color: rgba(255, 255, 255, 0.5);
    font-weight: 400;
  }

  .select:focus:invalid,
  .select-custom.isActive .select-custom__btn.invalid {
    // color: #B8B9D0;
  }

  .select option,
  .select-custom__options {
    color: #303030;
    background-color: #fff;
  }


  // ===== state =====

  @media (pointer: fine) {
    .select-wrap:hover:after {
      // border-top-color: $color-hover-1;
    }

    .select:hover,
    .select-wrap:hover .select-custom__btn {
      // color: #2A2929;
    background-color: rgba(#fff, .3);
    // border-color: $color-hover-1;
      border-color: #fff;
    }

    .select-wrap:hover .select:invalid,
    .select-wrap:hover .invalid {
      // color: $color-disable;
    }
    
    .select {
      opacity: 0;
    }

    .select:focus {
      opacity: 1;
    }

    .select-custom {
      display: block;
    }

    .select:focus+.select-custom {
      display: none;
    }
  }


  // === select-custom options ===

  .select-custom__options {
    position: absolute;
    top: rem(60);
    left: 0;
    display: none;
    width: 100%;
    padding: 10px 0;
    // border: 1px solid #6f6f6f;
    border-radius: 6px;
    // box-shadow: 0 0 4px #e9e1f8;
  }

  .select-custom.isActive .select-custom__options {
    display: block;
  }


  // === select-custom option ===

  .select-custom__option {
    position: relative;
    padding: 12px 5px 12px 20px;
    margin: 0px 2px;
    border-radius: 6px;
    cursor: pointer;
  }

  .select-custom__option:not(:last-of-type)::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    // border-bottom: 1px solid #d3d3d3;
  }

  // залипает как у native
  .select-custom__option.isHover {
    background-color: #7119C9;
    color: #fff;
  }

  .select-custom__option.isActive::before {
    // content: "✓";
    // position: absolute;
    // left: 5px;
  }
}