// ===== title =====

.title-1,
.title-2,
.title-3,
.title-4,
.title-5,
.title-6 {
  font-family: Play;
  font-weight: 700;
}


.title-1 {
  $title-1: 120;
  line-height: 139/$title-1;
  // margin-bottom: 40/$title-1 * 1em;
  @include adaptiv("font-size", $title-1, 50, 1);
  color: #fff;
  text-transform: uppercase;
}

.title-2 {
  $title-2: 40;
  line-height: 46/$title-2;
  margin-bottom: 40/$title-2 * 1em;
  @include adaptiv("font-size", $title-2, 22, 1);
  text-transform: uppercase;

  & span {
    color: #7119C9;
  }
}

.title-3 {
  $title-3: 24;
  line-height: 32/$title-3;
  // margin-bottom: 28/$title-3 * 1em;

  font-size: rem(20);
  @media (min-width: $sm) {
    font-size: rem($title-3);
  }
  // @include adaptiv("font-size", $title-3, 20, 1);
}

