// ===== hero ===== 

@if 1 {
  .hero {
    position: relative;
    margin-bottom: rem(40);

    &::before {
      content: "";
      display: block;
      width: 100%;
      height: rem(70);
      background-color: #7119C9;

      @media (min-width: $md) {
        height: rem(20);
      }

      @media (min-width: $lg) {
        height: calc((120rem - 100vw) / 15);
      }
    }

    & picture {
      display: none;

      @media (min-width: $md) {
        display: inline;
      }
    }
  }

  .hero__container {
    padding: 20px 15px;
    text-align: center;
    background-color: #9C44F4;

    @media (min-width: $md) {
      position: absolute;
      top: 58%;
      left: 50%;
      background-color: transparent;
      text-align: left;
      transform: translate(-50%, -50%);
    }

    @media (min-width: em(1344)) {
      top: 50%;
    }
  }

  .hero__suptitle {
    font-family: Play;
    font-weight: bold;
    font-size: rem(20);
    line-height: (23/20);
    letter-spacing: 0.08em;
    color: #12BFC5;
  }


  .hero__text {
    font-family: Play;
    font-weight: normal;
    line-height: 1.15;
    color: #FFFFFF;
    @include adaptiv("font-size", 40, 32, 1);
  }
}




// ===== best ===== 

@if 1 {
  .best {
    @include adaptiv("margin-bottom", 80, 50, 1);

  }

  .best__col {
    margin-bottom: 30px;

    @media (min-width: $md) {
      flex: 0 0 50%;
      margin-bottom: 40px;
    }

    @media (min-width: $lg) {
      flex: 0 0 25%;

      &:nth-child(even) {
        margin-top: 40px;
      }
    }
  }
}
 



// ===== catalog ===== 

@if 1 {
  .catalog {
    @include adaptiv("margin-bottom", 120, 80, 1);
  }

  .catalog__title {
    text-align: center;
  }

  .catalog__row {
    justify-content: center;
  }

  .catalog__product {
    margin-bottom: rem(30);

    @media (min-width: $md) {
      flex: 0 0 50%;
      margin-bottom: rem(40);
    }

    @media (min-width: $xl) {
      flex: 0 0 33.333333%;
    }
  }

  .product {
    display: flex;
    flex-direction: column;
  }

  .product__wrap {
    position: relative;
    padding-bottom: 107%;
    margin-bottom: 15px;
  }

  .product__info {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: flex-end;
    height: 100%;
    width: 100%;
    padding: 0px 0px 20px 20px;
    background: url(../img/bg-2.png) no-repeat center bottom / 100% auto;
  }

  .product__wrap-img {
    position: absolute;
    right: 30%;
    bottom: 0px;
    width: 60%;
    height: 100%;
    transform: translateX(50%);

    & img {
      height: 92%;
    }
  }

  .product__title-col {
    width: 60%;

    @media (min-width: em(400)) {
      width: 50%;
    }
  }

  .product__title {
    display: flex;
    justify-content: center;
    flex-direction: column;
    min-height: rem(64);
    margin-bottom: em(10);
    color: #fff;

    @media (min-width: $sm) {
      margin-bottom: em(20);
    }
  }

  .product__prices {
    display: flex;
    justify-content: center;
    flex-direction: column;
    min-height: rem(47);
    color: #EFEBFC;
    line-height: 1.1;
  }

  .product__price {
    font-weight: 400;
  }

  .product__price--old {
    font-size: rem(18);
    font-weight: 400;
    text-decoration: line-through;
  }

  .product__price--new {
    color: $color-2;
  }


  .product__text {
    flex-grow: 1;
    margin-bottom: 15px;
    font-size: rem(16);

    @media (min-width: $sm) {
      font-size: rem(18);
    }
  }
  .product__btn {
    @media (min-width: $sm) {
    align-self:flex-start;
    }
  }

  .btn-more {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: rem(50);
    color: $color-3;
    font-size: rem(14);
    font-weight: 700;
    text-transform: uppercase;
    font-family: $font-2;
    border: 2px solid #ebeffd;
    border-radius: 6px;
    transition: .1s ease 0s;

    &::before {
      margin-right: 10px;
      font-size: rem(24);
    }

    @media (pointer: fine) {
      &:hover {
        border-width: 1px;
        border-color: $color-3;
      }
    }

    &:active {
      border-width: 2px;
    }
  }

}



// ===== buy ===== 

@if 1 {
  .buy {
    margin-bottom: rem(40);
    padding-bottom: 40px;
    background: url(../img/bg-3.png) no-repeat;
    background-size: 1920px 1500px;
    background-position: left -450px bottom -450px;

    @media (min-width: $md) {
      padding-bottom: 270px;
      margin-bottom: 0px;
      background-position: left -300px bottom -140px;
      background-size: 1920px 1200px;
    }

    @media (min-width: $xl) {
      background-size: 1920px auto;
      background-position: center bottom;
    }
  }

  .buy__row {
    margin-bottom: rem(126);

    @media (min-width: $md) {
      margin-bottom: rem(146);
    }
  }

  .buy__col-title {
    margin-bottom: rem(30);

    @media (min-width: $xl) {
      flex: 0 0 41.666667%;
    }
  }

  .buy__col-img {
    margin-bottom: rem(30);

    @media (min-width: $md) {
      flex: 0 0 33.333333%;
    }

    @media (min-width: $xl) {
      flex: 0 0 16.666667%;
    }
  }

  .buy__title-3 {
    margin-bottom: rem(20);
  }

  .buy__col-text {
    margin-bottom: rem(10);

    @media (min-width: $md) {
      flex: 0 0 66.66667%;
    }

    @media (min-width: $xl) {
      flex: 0 0 41.666667%;
    }
  }

  .buns__col {
    margin-bottom: rem(30);

    @media (min-width: $md) {
      flex: 0 0 50%;
    }

    @media (min-width: $xl) {
      flex: 0 0 25%;

      &:nth-child(2) {
        margin-top: -40px;
      }

      &:nth-child(4) {
        margin-top: 40px;
      }
    }
  }

  .buns__text {
    display: flex;
    color: #fff;
    font-weight: bold;
    font-size: rem(19);
    line-height: 28/20;
    font-family: $font-2;

    &::before {
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60px;
      height: 60px;
      margin-right: rem(15);
      font-size: rem(30);
      color: $color-2;
      border-radius: 50%;
      background-color: #7119C9;
      box-shadow: inset 6px 0px 10px rgba(48, 48, 48, 0.3);
    }

  }

}



// ===== delivery ===== 

@if 1 {
  .delivery {
    @include adaptiv("margin-bottom", 80, 40, 1);
  }

  .delivery__col {
    margin-bottom: rem(40);

    @media (min-width: $md) {
      flex: 0 0 50%;
    }

    @media (min-width: $xl) {
      flex: 0 0 33.333333%;
    }
  }

  .delivery__list {
    & li {
      position: relative;
      padding-left: 32px;

      &:not(:last-child) {
        margin-bottom: rem(10);
      }

      & span {
        color: #303030;
      }

      &::before {
        content: "";
        position: absolute;
        top: 6px;
        left: 0px;
        display: block;
        width: 12px;
        height: 12px;
        background-image: url(../img/icon.svg);
      }
    }
  }
}



// ===== request ===== 

@if 1 {
  .request__row {
    position: relative;
    padding: 20px;
    background-image: linear-gradient(97.59deg, #7119C9 44.12%, #A149FA 100%);
    border-radius: 15px;
    margin: 0 -15px;

    @media (min-width: $md) {
      padding: 40px 40px 20px 40px;
      margin: 0 -20px;
    }

    @media (min-width: $xl) {
      margin: 0 -10px;
      padding-left: 33.333333%;
      background: url(../img/8.png) no-repeat left/ 29% 100%, linear-gradient(97.59deg, #7119C9 44.12%, #A149FA 100%);
    }
  }

  .request__icon {
    position: absolute;
    top: 50%;
    left: 26%;
    display: none;
    padding: 0;
    border-radius: 50%;
    background-color: $color-3;
    transform: translateY(-50%);

    @media (min-width: $xl) {
      display: flex;
    }
  }

  .request__title {
    color: #fff;
  }

  .request__text {
    font-weight: bold;
    color: #FFFFFF;
    font-family: $font-2;
    font-size: rem(18);
    line-height: (30/20);
    margin-bottom: 18px;

    @media (min-width: $md) {
      font-size: rem(20);

    }
  }


  .request__form {
    color: #fff;
  }

  .request__form-item {
    margin-bottom: 20px;
  }

  .request__btn {
    width: 100%;

    @media (min-width: $md) {
      flex: 0 0 auto;
      width: auto;
      align-self: flex-start;
    }
  }
}