// ===== popper  ===== 

@if null {
  .tooltip {
    // display: none;
    padding: 4px 8px;
    color: white;
    font-weight: bold;
    font-size: 13px;
    border-radius: 4px;
    background: #333;
    opacity: 0;
    visibility: hidden;
    transition: .2s ease 0s;
  }

  .tooltip.show {
    // display: block;
    opacity: 1;
    visibility: visible;
  }

  .tooltip-arrow,
  .tooltip-arrow::before {
    position: absolute;
    width: 8px;
    height: 8px;
    background: inherit;
  }

  .tooltip-arrow {
    visibility: hidden;
  }

  .tooltip-arrow::before {
    content: '';
    visibility: visible;
    transform: rotate(45deg);
  }

  .tooltip[data-popper-placement^='top']>.tooltip-arrow {
    bottom: -4px;
  }

  .tooltip[data-popper-placement^='bottom']>.tooltip-arrow {
    top: -4px;
  }

  .tooltip[data-popper-placement^='left']>.tooltip-arrow {
    right: -4px;
  }

  .tooltip[data-popper-placement^='right']>.tooltip-arrow {
    left: -4px;
  }
}



// ===== tooltip CSS ===== 

@if 1 {
  .tooltip-btn {
    position: relative;
    display: inline;
    border-bottom: 2px dotted #3D414C;
  }

  .tooltip {
    position: absolute;
    z-index: 1;
    top: -5px;
    left: 50%;
    padding: 5px;
    color: #fff;
    white-space: nowrap;
    border-radius: 6px;
    background-color: black;
    visibility: hidden;
    opacity: 0;
    transition: .2s ease 0s;
    transform: translate(-50%, -100%);
  }

  .tooltip::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    border: 5px solid transparent;
    border-top-color: black;
    transform: translateX(-50%);
  }

  .tooltip-btn:hover .tooltip {
    opacity: 1;
    visibility: visible;
  } 
}