// ==== General ==== 

html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	-webkit-text-size-adjust: 100%;
	-ms-overflow-style: scrollbar;
}

body {
	height: 100%;
	width: 100%;
	// min-width: 320px;
	// overflow-x: hidden;
	font-family: 'Fira Sans', sans-serif;
	font-size: rem(16);
	// font-weight: 300;
	line-height: 1.4;
	color: #303030;

	@media (min-width: $md) {
		font-size: rem(18);
	}
}

body.lock {
	overflow: hidden;
}


.wrapper {
	// padding-top: 60px;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100%;
	// background-color: #F5F5F8;
	// overflow: hidden;

	@media (min-width: $md) {
		// padding-top: 120px;
	}
}


.main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

._cover,
._contain {
	position: relative;
}

._cover img,
._contain img {
	position: absolute;
	height: 100%;
	width: 100%;
}

._cover img {
	object-fit: cover;
	// font-family: 'object-fit: cover;';
}

._contain img {
	object-fit: contain;
	// font-family: 'object-fit: contain;';
}


.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
	visibility: hidden;
}

.hidden {
	display: none !important;
}

.page {
	padding: 15px 0px 60px 0px;

	@media (min-width: $md) {
		padding: 30px 0px 120px 0px;
	}
}

p {
	margin-bottom: rem(10);
}