@mixin font($font_name, $file_name, $weight, $style) {

	@font-face {
		font-family: $font_name;
		font-display: swap;
		src: url("../fonts/#{$file_name}.woff2") format("woff2"),
		url("../fonts/#{$file_name}.woff") format("woff");
		font-weight: #{$weight};
		font-style: #{$style};
	}
}


// ===== adaptiv size =====

//$maxWidth: 1920; //макетная ширина с исходными параметрами
//$maxWidthContainer: 1270; // ширина с/до которой изменять параметр

// $type значение где изменять параметр
// 0 - на любой ширине
// 1 - до maxWidthContainer
// 2 - после maxWidthContainer

@mixin adaptiv($property, $pcSize, $mobSize, $type) {
	$addSize: $pcSize/16 - $mobSize/16;

	@if $type==1 {
		// если меньше контейнера
		#{$property}: $pcSize/16 + rem;

		@media (max-width: #{$maxWidthContainer/16 + rem}) {
			#{$property}: calc(#{$mobSize/16 + rem} + #{$addSize} * ((100vw - #{$minWidth/16 + rem}) / #{$maxWidthContainer/16 - $minWidth/16}));
		}
	}

	@else if $type==2 {
		// если больше контейнера
		#{$property}: $mobSize/16 + rem;

		@media (min-width: #{$maxWidthContainer/16 + rem}) {
			#{$property}: calc(#{$mobSize/16 + rem} + #{$addSize} * ((100vw - #{$maxWidthContainer/16 + rem}) / #{$maxWidth/16 - $maxWidthContainer/16}));
		}
	}

	@else {
		// всегда
		#{$property}: calc(#{$mobSize/16 + rem} + #{$addSize} * ((100vw - #{$minWidth/16 + rem}) / #{$maxWidth/16 - $minWidth/16}));
	}
}


