@function rem($px) {
	// 	$result: math.div($px, 16) + rem;
	$result: ($px / 16) + rem;
	@return $result;
}

@function em($px) {
	$result: ($px / 16) + em;
	@return $result;
}

@function prc($pxOne, $pxTwo) {
	$result: ($pxOne / $pxTwo) * 100%;
	@return $result;
}

