// ===== circle ===== 

@if 1 {
  .circle__wrap-img {
    position: relative;
    display: inline-block;
    border-radius: 50%;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: inset 6px 0px 10px rgba(179, 179, 179, 0.3);

    & img {
      width: 150px;
      height: 150px;
      height: rem(150);
      width: rem(150);
      border-radius: 50%;
      object-fit: cover;
    }
  }

  .circle__icon {
    position: absolute;
    right: 0px;
    top: 50%;
    transform: translateY(-50%);

    &::before {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      font-size: 24px;
      color: $color-2;
      border-radius: 50%;
      background: linear-gradient(99.69deg, #7119C9 0%, #A149FA 100%);
      box-shadow: 6px 6px 12px rgba(113, 25, 201, 0.3);
    }
  }

  .circle__desc {
    font-family: Play;
    font-size: rem(16);

    @media (min-width: $md) {
      font-size: rem(20);
    }
  }

  .circle__title {
    font-weight: bold;
    margin-bottom: 10px;
  }

  .circle__text {
    color: #A9A9A9;
  }
}



.icon-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: inset 6px 0px 10px rgba(48, 48, 48, 0.3);

  @media (min-width: $lg) {
    display: flex;
  }

  &::before {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    font-size: 24px;
    color: $color-2;
    border-radius: 50%;
    background: linear-gradient(135deg, #A149FA 0%, #7119C9 100%);
    box-shadow: 6px 6px 12px rgba(113, 25, 201, 0.3);
  }
}



.contact {
  display: flex;
  align-items: center;
  font-family: Play;
  font-weight: bold;
  font-size: rem(18);
}

.contact__icon {
  margin-right: 20px;
}