// ===== grid ===== 

.container {
	width: 100%;
	max-width: rem($maxWidthContainer);
	padding: 0 15px;
	margin: 0 auto;
}

@media (min-width: $md) {
	.container {
		padding: 0 20px;
	}
}

// .container--small {
// 	max-width: 1000px;
// }

.container-md {
	// @media (min-width: $md) {
	// 	width: 100%;
	// 	max-width: 1270px;
	// 	padding: 0 15px;
	// 	margin: 0 auto;
	// }
}


.row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -5px;

	@media (min-width: $md) {
		margin: 0 -10px;
	}
}

.row>* {
	flex: 0 0 100%;
	padding: 0 5px;

	@media (min-width: $md) {
		padding: 0 10px;
	}
}

.col {
	flex: 1 1 0;
}

.col-auto {
	flex: 0 0 auto;
}

.col-3 {
	flex: 0 0 25%;
}

.col-6 {
	flex: 0 0 50%;
}

.col-8 {
	flex: 0 0 66.66667%;
}

.col-4 {
	flex: 0 0 33.333333%;
}

.col-12 {
	flex: 0 0 100%;
}

.order-1 {
	order: 1;
}

@media (min-width: $sm) {
	.col-md {
		flex: 1 1 0;
	}

	.col-sm-6 {
		flex: 0 0 50%;
	}

	.order-sm-1 {
		order: 1;
	}
}

@media (min-width: $md) {
	.col-md-auto {
		flex: 0 0 auto;
	}

	.col-md-3 {
		flex: 0 0 25%;
	}

	.col-md-4 {
		flex: 0 0 33.333333%;
	}

	.col-md-5 {
		flex: 0 0 41.666667%;
	}

	.col-md-6 {
		flex: 0 0 50%;
	}

	.col-md-7 {
		flex: 0 0 58.333333%;
	}

	.col-md-8 {
		flex: 0 0 66.66667%;
	}

	.order-md-0 {
		order: 0;
	}

	.order-md-1 {
		order: 1;
	}

	.order-md--1 {
		order: -1;
	}

	.order-md--2 {
		order: -2;
	}
}

@media (min-width: $xl) {
	.col-xl {
		flex: 1 1 0;
	}

	.col-xl-2 {
		flex: 0 0 16.666667%;
	}

	.col-xl-3 {
		flex: 0 0 25%;
	}

	.col-xl-4 {
		flex: 0 0 33.333333%;
	}

	.col-xl-5 {
		flex: 0 0 41.666667%;
	}

	.col-xl-6 {
		flex: 0 0 50%;
	}

	.col-xl-7 {
		flex: 0 0 58.333333%;
	}

	.col-xl-9 {
		flex: 0 0 75%;
	}

	.col-xl-12 {
		flex: 0 0 100%;
	}

	.order-xl-0 {
		order: 0;
	}
}