// ===== rating svg =====

@if null {
  .rating {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 60px;
    padding: 0 20px;
    // margin-bottom: 20px;
    background-color: #fff;
    border: 2px solid transparent;
    border-radius: 30px;
    transition: .2s ease 0s;

    @media (min-width: 768px) {
      padding: 0 30px;
    }
  }

  .rating.invalid {
    border-color: #eb5757;
  }

  .rating:hover {
    border-color: $color-hover-1;
  }

  .rating__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 28px;
    text-transform: uppercase;

    @media (min-width: $md + px) {
      font-size: 14px;
      line-height: 28px;
    }
  }

  .rating__group {
    display: inline-flex;
    padding-top: 5px;
  }

  .rating.focus,
  .rating__group.focus {
    border-color: $color-hover-1;
    // box-shadow: white 0 0 0 2px, lightskyblue 0 0 0 5px;
  }

  .rating__label:first-of-type {
    display: none;
  }

  .rating__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }

  .rating__input:focus.focus+.rating__label {
    box-shadow: none;
  }

  .rating__label {
    cursor: pointer;
  }

  .rating__label:before {
    content: url(../img/star.svg);
    margin-right: 5px;
  }

  .rating__label:last-child::before {
    margin-right: 0;
  }

  .rating__input:checked+.rating__label~.rating__label:before {
    content: url(../img/star-bg.svg);
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .rating__group:hover .rating__label[for]::before {
      content: url(../img/star.svg);
    }

    .rating__group:hover .rating__label[for]:hover::before {
      content: url(../img/star-hover.svg);
    }

    .rating__group:hover .rating__label:hover~.rating__label:before {
      content: url(../img/star-bg.svg);
    }
  }
}


// ===== rating ===== 

@if 1 {
  .rating {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 50px;
    padding: 0 20px;
    background-color: #F3F4F8;
    border: 2px solid transparent;
    border-radius: 5px;
    transition: .2s ease 0s;

    @media (min-width: 768px) {
      min-height: 60px;
    }
  }

  .rating.invalid {
    border-color: #eb5757;
  }

  .rating:hover {
    border-color: $color-1;
  }

  .rating__text {
    font-weight: 500;
    font-size: 12px;
    line-height: 28px;
    text-transform: uppercase;

    @media (min-width: $md + px) {
      font-size: 14px;
      line-height: 28px;
    }
  }

  .rating__group {
    display: inline-flex;
    padding-top: 5px;
  }

  .rating.focus,
  .rating__group.focus {
    border-color: $color-1;
    // box-shadow: white 0 0 0 2px, lightskyblue 0 0 0 5px;
  }


  .rating__label:first-of-type {
    display: none;
  }

  .rating__input {
    position: absolute;
    width: 1px;
    height: 1px;
    opacity: 0;
    clip: rect(0 0 0 0);
  }

  .rating__input:focus.focus+.rating__label {
    box-shadow: none;
  }

  .rating__label {
    cursor: pointer;
  }

  .rating__label:before {
    content: "★";
    display: block;
    font-size: 25px;
    // letter-spacing: 10px;
    line-height: 1;
    color: #F76A17;
    transition: .2s ease 0s;
  }

  .rating__input:checked+.rating__label~.rating__label:before {
    color: #C0C5D1;
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .rating__group:hover .rating__label[for]:before {
      color: #F76A17;
    }

    .rating__group:hover .rating__label:hover~.rating__label:before {
      color: #C0C5D1;
    }
  }
}