// ===== head ===== 

@if 1 {
  .head {
    position: fixed;
    z-index: 4;
    left: 0;
    top: 0;
    width: 100%;
    transition: transform .3s ease 0s;

    &.hide:not(.active) {
      transform: translateY(-100%);
    }
  }

  .head {
    height: rem(70);
  }

  .overlay,
  .navbar {
    position: fixed;
    top: rem(70);
    left: 0px;
  }

  .navbar {
    height: calc(100% - #{rem(70)});
  }

  @media (min-width: $lg) {
    .head {
      height: rem(135);
    }

    .overlay,
    .navbar {
      top: rem(135);
    }

    .navbar {
      height: calc(100% - #{rem(135)});
    }


    .head.scroll {
      height: rem(100);

      .navbar {
        top: rem(100);
        height: calc(100% - #{rem(100)});
      }
    }

    .overlay.scroll {
      top: rem(100);
    }
  }


  //=== scroll ===

  .head.scroll {
    background-color: #7119C9;

    .logo__wrap {
      width: 70px;
      height: 70px;
      margin-top: 0;

      @media (min-width: $lg) {
        width: 100px;
        height: 100px;
      }
    }

    .logo__img {
      width: 50px;
      height: 50px;

      @media (min-width: $lg) {
        width: 70px;
        height: 70px;
      }
    }
  }


  .head__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0 5px;

    @media (min-width: $md) {
      padding: 0 10px;
    }
  }


  .logo {
    flex-grow: 1;
    flex-shrink: 0;
    position: relative;
    display: flex;
    align-items: center;
    margin-right: 20px;
  }

  .logo__wrap {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: #7119C9;
    box-shadow: inset 6px 0px 10px rgba(48, 48, 48, 0.3);
  }

  @media (min-width: $lg) {
    .logo__wrap {
      width: 170px;
      height: 170px;
      margin-top: 40px;
    }

    .logo__img {
      width: 130px;
      height: 130px;
    }
  }

  .logo__text {
    display: none;
    font-family: $font-2;
    color: $color-2;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;

    @media (min-width: $md) {
      display: block;
    }
  }


  .logo__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


  .head__tel {
    flex-grow: 1;
    justify-content: center;
    margin: 0px 20px 0px 0px;
    color: #fff;

    @media (pointer: fine) {
      &-link:hover {
        color: $color-hover-2;
      }
    }

    &-link:active {
      color: $color-active-2;
    }
  }


  .head__tel-icon {
    display: none;
    margin-right: 20px;

    @media (min-width: $lg) {
      display: flex;
    }

    &::before {
      box-shadow: 6px 6px 12px rgba(113, 25, 201, 0.3);
    }
  }

  .head__btn {
    display: none;
    margin: 0px 20px 0px 0px;

    @media (min-width: 400px) {
      display: inline-flex;
    }

    @media (min-width: $xl) {
      margin: 0px 0px 0px 0px;
    }
  }
}




//  menu  desktop + mobile js 

@if 1 {
  .overlay {
    z-index: 2;
    right: 0;
    bottom: 0;
    display: none;
    background-color: rgba(36, 57, 73, .6);

    &.active {
      display: block;

      @media (min-width: $xl) {
        display: none;
      }
    }
  }


  .navbar {
    z-index: 3;
    width: 100%;
    max-width: 360px;
    overflow: auto;
    transition: .2s ease 0s;
    transform: translateX(-100%);

    &.active {
      transform: translateX(0);
    }
  }


  .navbar__body {
    min-height: 100%;
    width: 100%;
    padding: 15px 35px;
    font-size: 22px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    background-color: #0277DE;
  }

  .menu-start,
  .head__tel-start {
    flex-grow: 1;
  }


  @media (min-width: $xl) {
    .menu {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      color: #fff;
    }

    .menu li {
      &+li {
        margin-left: 10px;
      }
    }
  }

  .menu a {
    display: inline-block;
    width: 100%;
    padding: 10px 0;
    transition: .2s;

    @media (pointer: fine) {
      &:hover {
        color: $color-hover-2;
      }
    }

    &:active {
      color: $color-active-2;
    }
  }

}




// ===== burger ====== 

@if 1 {
  .wrap__burger {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #ff9d5c;
  }

  .burger {
    position: relative;
    z-index: 3;
    display: inline-block;
    width: 24px;
    height: 17px;
  }

  @media (min-width: $xl) {
    .wrap__burger {
      display: none;
    }

    .burger {
      display: none;
    }
  }

  .burger::before,
  .burger::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #303540;
    transition: .3s ease 0s;
  }

  .burger::before {
    top: 0;
  }

  .burger::after {
    bottom: 0;
  }

  .burger.active::before {
    top: 7px;
    transform: rotate(45deg);
  }

  .burger.active::after {
    bottom: 7px;
    transform: rotate(-45deg);
  }

  .burger span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    top: 7px;
    background-color: #303540;
    transition: .3s ease 0s;
  }

  .burger.active span {
    transform: scale(0);
  }
}