// ===== footer ===== 

@if 1 {
	.footer {
		padding: 40px 0;
	}

	.footer__row {
		justify-content: space-between;
		align-items: center;
	}

	.footer__col {
		margin-bottom: 20px;

		@media (min-width: $lg) {
			&:nth-child(2) {
				margin-right: 40px;
			}
		}
	}

	.footer__col-policy {
		font-size: rem(16);
		color: #A9A9A9;

		@media (min-width: $lg) {
			flex: 1 0 0;
		}
	}

	.policy {
		line-height: (24/16);
		text-decoration-line: underline;
	}

	.footer__link {
		@media (pointer: fine) {
			&:hover {
				color: $color-hover-3;
			}
		}

		&:active {
			color: $color-active-3;
		}
	}

}