// === sending ===

.form {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../img/loading.gif) no-repeat rgba(59, 59, 59, 0.8) center/50px;
    opacity: 0;
    visibility: hidden;
    transition: .2s ease 0s;
  }

  &.sending::after {
    opacity: 1;
    visibility: visible;
  }
}


.form__item {
  position: relative;

  &-rating {
    position: relative;
  }

  &--icon-mod::before,
  &--icon-page::before {
    position: absolute;
    top: 50%;
    left: 20px;
    font-size: 22px;
    transform: translateY(-50%);
  }

  &--icon-page {
    &::before {
      color: #fff;
    }

    &.focus::before {
      color: $color-1;
    }

    @media (pointer: fine) {
      &:hover:before {
        // color: $color-1;
      }
    }
  }

  &--icon-mod {
    &::before {
      color: #C0C5D1;
    }

    &.focus::before {
      color: $color-1;
    }

    @media (pointer: fine) {
      &:hover:before {
        color: $color-1;
      }
    }
  }

  // required

  &--req::after {
    position: absolute;
    top: 10px;
    right: 15px;
    content: '★';
    display: block;
    font-size: 15px;
    // letter-spacing: 10px;
    line-height: 1;
    color: #C0C5D1;
  }
}


// ===== input =====

input::placeholder,
textarea::placeholder {
  font-family: $font-2;
  // color: #b8b9d0;
  text-transform: uppercase;
  font-weight: 500;
  opacity: 1;
  text-overflow: ellipsis;
}

input:hover::placeholder,
input:focus::placeholder,
textarea:hover::placeholder,
textarea:focus::placeholder {
  // color: #b8b9d0;
}

.form__input {
  display: block;
  width: 100%;
  height: rem(40);
  padding: 0px 20px;
  // padding-left: 60px;

  font-size: rem(14);
  // font-weight: 600;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-family: $font-2;

  border-radius: 6px;
  border: 1px solid transparent;
  transition: .1s ease 0s;

  @media (min-width: $md) {
    height: rem(50);
  }

  &--page {
    color: #fff;
    background-color: #863BD1;
    background-color: rgba(#fff, .15);


    &::placeholder {
      color: #fff;
    }

    &:focus {
      // color: #3D414C;
      border-color: #fff;
      background-color: rgba(#fff, .3);
    }

    @media (pointer: fine) {
      &:hover {
        border-color: #fff;
        background-color: rgba(#fff, .3);
      }
    }
  }

  &--mod {
    background-color: #F3F4F8;

    &::placeholder {
      // color: #fff;
    }

    &:focus {
      border-color: $color-1;
      // background-color: #fff;
    }

    @media (pointer: fine) {
      &:hover {
        border-color: $color-1;
      }
    }
  }
}


.form__input-textarea {
  height: rem(80);
  padding: 16px 20px;

  @media (min-width: $md) {
    height: rem(180);
  }
}


// ===== checkbox ===== 

@if 1 {
  .check__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
  }

  .check__label {
    position: relative;
    display: block;
    padding-left: 34px;
    margin-bottom: 10px;
    // color: #fff;
    cursor: pointer;
  }

  .check__label::before {
    content: '';
    position: absolute;
    left: 0;
    top: 2px;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    // background-color: rgba(#fff, 0.1);
    // box-shadow: -12px -12px 24px #7d82ff, 12px 12px 24px #5d60d4;
  }

  .check__input:checked+.check__label:before {
    background: center/100%;
    background-image: url(../icon.svg#icon-check),
      linear-gradient(180deg, #5cdeda 0%, #2fff6a 100%);
  }

  @media (pointer: fine),
  (min-width: 0\0) {
    .check__label:hover::before {
      border: 2px solid #5cdeda;
    }
  }
}



// === Hide/Show Password  ===

.password-control {
  position: absolute;
  bottom: 15px;
  right: 30px;
  width: 30px;
  height: 30px;
  font-size: 24px;
  color: $color-gray;
  transition: .1s ease 0s;

  @media (pointer: fine) {
    &:hover {
      color: $color-hover-1;
    }
  }
}