// ====  modal =====

.no-scroll {
  overflow-y: auto !important;
}

// body
.has-dialog {
  overflow: hidden;
}


// =====  ===== 

@if 1 {
  .dialog-backdrop {
    position: fixed;
    z-index: 100;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    background: linear-gradient(0deg, rgba(113, 25, 201, 0.75), rgba(113, 25, 201, 0.75));
    opacity: 0;
    visibility: hidden;
    transition: .3s ease 0s;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }


  .modal {
    position: relative;
    // width: 100%;
    width: calc(100% - (10px * 2));
    max-width: rem(400);
    max-height: 100%;
    transition: .3s ease 0s;
    transform: translate(0%, -20%);

    &.active {
      transform: translate(0%, 0);
    }
  }


  .modal__content {
    padding: 30px 15px;
    background-color: #EFEBFC;
    border-radius: 15px;
    overflow: auto;

    @media (min-width: em(420)) {
      padding: 40px;
    }

    & img {
      width: 80px;
      height: 80px;
      margin-right: 10px;
      object-fit: contain;
    }
  }


  .modal__close {
    position: absolute;
    top: 15px;
    right: 15px;
    color: #C0C5D1;

    & svg {
      width: 14px;
      height: 14px;
    }

    @media (min-width: $md) {
      top: -20px;
      right: -20px;
      color: #fff;
    }

  }

  .modal__head {
    display: flex;
  }

  .modal__title {
    font-family: Play;
    font-style: normal;
    font-weight: bold;
    font-size: rem(40);
    line-height: (46/40);
    text-transform: uppercase;
  }
.modal__title--request {
  font-size: rem(24);
  margin-bottom: rem(20);
}

  .modal__prices {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 40px;
    font-family: Play;
    font-size: rem(16);
    line-height: (19/16);
  }

  .modal__price-old {
    margin-right: 10px;
    text-decoration-line: line-through;
    color: $color-gray;
  }

  .modal__prices-new {
    font-weight: bold;
    font-size: rem(20);
    line-height: (23/20);
    color: #D52331;
  }

  .modal__form {
    margin-bottom: 10px;
  }

  .modal__form-item {
    margin-bottom: 10px;
  }

  .modal__btn {
    width: 100%;
  }

  .modal__text {
    margin-bottom: 20px;
    font-size: rem(16);
    line-height: (24/16);
    color: $color-gray;
  }

  .modal__link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $font-2;
    font-weight: 700;
    font-size: rem(14);
    color: $color-3;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    &::before {
      margin-right: 10px;
      font-size: rem(24);
    }
  }

  .modal__title--ok {
    text-align: center;
    margin-bottom: 20px;
  }

  .modal__text--ok {
    margin-bottom: 40px;
    text-align: center;
  }
}